var config = {
    tree1 : {
        sprite : ['tree1', [0, 0], [62, 87]],
        size : [62, 88],
        rules: ['dynamicZIndex']
    },
    tree2 : {
        sprite : ['tree2', [0, 0], [59, 87]],
        size : [60, 88],
        rules: ['dynamicZIndex']
    },
    stones : {
        render: 'object',
        sprite : ['stone', [0, 0], [25, 22]],
        size : [15, 22],
        rules : ['dynamicZIndex']
    }
};

export default config;


